/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  background-color: #ededed;
  font-family: "Roboto", sans-serif;
  font-size: 22px;
}

.app {
  text-align: center;
  color: black;
}

.site-header {
  height: 85vh;
  margin-bottom: 80px;
}
.background-header-pic {
  width: 100%;
  height: 85vh;
  margin: 0 auto;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.35;
  box-shadow: 0px 7px 6px 2px rgba(0, 0, 0, 0.9);
}

.header-logo-container {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.header-logo-box {
  display: flex;
  justify-self: center;
  align-self: center;
}

.text-under-logo {
  margin-top: 20px;
  margin-left: 20px;
  display: flex;
  justify-self: center;
  align-self: center;
  text-transform: uppercase;
  font-size: 22px;
  font-weight: 900;
  letter-spacing: 5px;
  color: black;
  text-shadow: 1px 1px gray;
}

.nav-list {
  display: flex;
  justify-content: flex-end;
  padding: 25px;
}

.nav-list li {
  list-style-type: none;
  font-size: 20px;
  font-weight: 520;
  margin: 12px;
}

.nav-list a {
  color: black;
  text-decoration: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.nav-list a:hover {
  cursor: pointer;
  color: #3b9998;
}

.nav-list li img {
  margin-top: -5px;
  border-radius: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.nav-list li:hover img {
  opacity: 0.5;
}

.highlights {
  margin: 30px 150px;
}

.highlights-container {
  background-color: white;
  color: #142239;
}
.highlights-slogans {
  padding: 20px;
  margin-bottom: 30px;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.counter {
  font-style: bold;
  font-size: 22px;
  color: green;
  text-shadow: 0px -4px 3px #67677a;
}

.slogan {
  padding: 10px;
  line-height: 40px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 600px;
  text-shadow: 1px 1px darkgray;
}

.highlights-text {
  margin: 15px;
  font-weight: 0;
  line-height: 25px;
  letter-spacing: 2px;
}

.highlights-meetings-container {
  background-color: #ededed;
  margin: 80px 0;
}
.soonest-meetings {
  display: flex;
  justify-content: space-between;
  line-height: 25px;
}
.soonest-meeting-text {
  background-color: white;
  padding: 20px;
  text-transform: uppercase;
  color: #142239;
  margin-bottom: 30px;
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.meeting-box {
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
  opacity: 0.85;
  display: flex;
  flex-direction: column;
  z-index: 100;

  padding: 20px;
  width: 45%;
  position: relative;
  color: white;
  overflow: hidden;
  -webkit-transition: 0.3 ease-in-out;
  transition: 0.3 ease-in-out;
}

.meeting-box:hover {
  -webkit-transition: 0.3;
  transition: 0.3;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.meeting-box-one {
  margin-right: 20px;
}

.meeting-box-two {
  margin-left: 20px;
}

.meeting-city {
  font-style: bold;
  padding-bottom: 10px;
  font-weight: 900;
  font-size: 20px;
  text-shadow: 3px -5px 3px black;
}
.meeting-background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-size: cover;
  margin: 0 auto;
}

.demands {
  margin: 30px 150px;
}

.demands-container {
  background-color: #ededed;
  margin-top: 30px;
}

.demands-conatiner-text {
  padding: 20px;
  background-color: white;
  text-transform: uppercase;
  color: #142239;
  margin-bottom: 30px;
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.demand-boxes-container {
}

.demands-row {
  display: flex;
  width: 100%;
}

.demands-box {
  height: 68vh;
  width: 31.5%;
  background-color: white;
  box-shadow: 10px 10px 5px -5px rgba(0, 0, 0, 0.75);
}

.demands-box-1 {
  margin-right: 30px;
}

.demands-box-2 {
  margin-right: 30px;
}
.demands-img-container {
  width: 100%;
  overflow: hidden;
  height: 190px;
}
.demands-img {
  opacity: 0.7;
}
.demands-title {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 18px;
  padding: 20px;
  color: #142239;
  font-weight: 600;
  line-height: 25px;
}
.demands-description {
  padding: 0 30px;
  text-align: justify;
  line-height: 20px;
  font-size: 15px;
  color: #142239;
}

.button-container {
  font-size: 14px;
  text-align: center;
}

.button-text-box {
  text-align: center;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: darkblue;
  color: white;
  margin-top: 15px;
  border-radius: 4px;
  padding: 9px 15px;
  display: inline-block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.button-text-box:hover {
  cursor: pointer;
  opacity: 0.8;
}

